import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { map } from 'rxjs/operators';


@Injectable({
  providedIn: 'root'
})
export class DataService {
 public url = 'http://data.ntpc.gov.tw/api/v1/rest/datastore/382000000A-000070-002';

  constructor(private http: Http) { }
  public getData() {
    return this.http.get(this.url).pipe(map((res) => {return res.json() || {};
  }));
 }
}
