import { Component, ViewChild, ElementRef, NgZone } from '@angular/core';
import { GooglePlaceDirective } from 'ngx-google-places-autocomplete/ngx-google-places-autocomplete.directive';
declare const require: any;
import { MapsAPILoader } from '@agm/core';
import { FormControl } from '@angular/forms';
import { DataService } from './data.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  providers: [DataService]
})
export class AppComponent {
  title: String = 'My first AGM project';
  getData: Object;
  records = [];
  objectKeys = Object.keys;
  public lat: Number = 25.0124794;
  public lng: Number = 121.46569199999999;
  zoomValue: Number = 18;
  options = [];
  public searchControl: FormControl;
  public geocoder = new google.maps.Geocoder();
  @ViewChild('placesRef') placesRef: GooglePlaceDirective;
  @ViewChild('search')
  public searchElementRef: ElementRef;
  handleAddressChange($event) {
    this.lat = $event.geometry.location.lat();
    this.lng = $event.geometry.location.lng();
    this.zoomValue = 16;
  }
  constructor(
    private mapsAPILoader: MapsAPILoader,
    private ngZone: NgZone,
    private dataService: DataService
  ) {}

  // tslint:disable-next-line:use-life-cycle-interface
  ngOnInit() {
    this.dataService.getData().subscribe(result => {
      if (
        typeof (result.result === 'object') &&
        typeof (result.result.records === 'object')
      ) {
        this.getData = this.objectKeys(result.result);
        this.records = result.result['records'];
        console.log(this.records);
      }
    });
  }

   onKeydown(event) {
    if (event.key === 'Enter') {
      this.geocoder.geocode(
        { address: event.srcElement.value },
        (results, status) => {
          if (String(status) === 'OK') {
            this.lat = results[0].geometry.location.lat();
            this.lng = results[0].geometry.location.lng();
            this.dataService.getData().subscribe(result => {
              this.getData = Array.of(result[result]);
            });
          } else {
            console.log(status);
          }
        }
      );
    }
  }

   setCurrentPosition() {
    if ('geolocation' in navigator) {
      navigator.geolocation.getCurrentPosition((position) => {
        this.lat = position.coords.latitude;
        this.lng = position.coords.longitude;
        this.zoomValue = 16;
      });
    }
  }


}
